.drop-file-input{
    position: relative;
    width: 400px;
    height: 200px;
    border: 2px dashed var(--border-color);
    border-radius: 20px;

    display: flex;
    align-items: center;
    justify-content: center;

    background-color: var(--input-bg);
}

.drop-file-input input {
    opacity: 0;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    cursor: pointer;
}

.drop-file-input:hover, 
.drop-file-input.dragover {
    opacity: 0.6;
}

.drop-file-input__label{
    
    text-align: center;
    color: var(--txt-second-color);
    font-weight: var(--font-medium);
    padding: 10px;
}

.drop-file-preview {
    /*margin-top: 30px;*/
}

.drop-file-preview p {
    font-weight: 500;
}

.drop-file-preview__title {
    margin-bottom: 10px;
    margin-top: 20px;
}

.drop-file-preview__item {
    position: relative;
    display: flex;
    margin-bottom: 10px;
    background-color: var(--input-bg);
    padding: 15px;
    border-radius: 20px;
}

.download_icon {
    height: 1.25rem;
    margin-right: 1rem;
}

.img_mp3{
    height: 3rem;
    margin-right: 1rem;
}

.drop-file-preview__item__info {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.drop-file-preview__item__del {
    background-color: var(--box-bg);
    width: 40px;
    height: 40px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    right: 10px;
    top: 50%;
    transform: translateY(-50%);
    box-shadow: var(--box-shadow);
    cursor: pointer;
    opacity: 0;
    transition: opacity 0.3s ease;
}

.drop-file-preview__item:hover .drop-file-preview__item__del {
    opacity: 1;
}
.podpis{
    color: var(--txt-second-color);
    font-weight: var(--font-normal);
    font-size: var(--small-font-size);
}
.input__token{
    width: 250px;
    background-color: var(--input-bg);
    border: none;
    padding: 0.5rem 1rem;
    border-radius: 1rem;
    /*margin-bottom: 10px;*/
}